html {
  font-size: 1rem;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd,
input,
fieldset {
  margin: 0;
}

fieldset {
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.1;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --clr-primary-100: #1db489;
  --clr-primary-200: #dc414c;
  --clr-primary-300: #198ff5;
  --clr-primary-400: #1ca0f2;
  --clr-primary-500: #c4032a;
  --clr-darktheme-100: #1e202a;
  --clr-darktheme-200: #1f212e;
  --clr-darktheme-300: #252a41;
  --clr-darktheme-400: #8b97c6;
  --clr-darktheme-500: white;
  --clr-darktheme-600: #333a57;
  --clr-lighttheme-50: #aeb3cb;
  --clr-lighttheme-100: white;
  --clr-lighttheme-200: #f5f7ff;
  --clr-lighttheme-300: #f0f2fa;
  --clr-lighttheme-400: #63687e;
  --clr-lighttheme-500: #1e202a;
  --clr-lighttheme-600: #e0e2f0;
  --clr-primarylinear-100: #fdc468, #df4996;
  --clr-darkthemelinear-100: #378fe6, #3eda82;
}
@media only screen and (min-width: 40em) {
  :root {
    --fz-50: 0.625rem;
    --fz-75: 0.75rem;
    --fz-100: 1rem;
    --fz-200: 1.125rem;
    --fz-250: 1.25rem;
    --fz-300: 1.5rem;
    --fz-400: 1.75rem;
    --fz-500: 1.953rem;
    --fz-600: 2.441rem;
    --fz-650: 2.625rem;
    --fz-700: 3.052rem;
  }
}

:root {
  --font-inter: "Inter", sans-serif;
}

body {
  font-family: var(--font-inter);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--clr-basetext);
}

body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.dashboard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 59.375rem;
}
.dashboard > .detalhes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
@media only screen and (max-width: 70em) {
  .dashboard {
    max-width: 29.375rem;
  }
}
@media only screen and (max-width: 40em) {
  .dashboard {
    max-width: 12.5rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
}
@media only screen and (max-width: 40em) {
  .header {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header > .left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 20.2rem;
}
@media only screen and (max-width: 40em) {
  .header > .left {
    min-width: 0;
  }
}
.header > .left > .titulo {
  font-size: var(--fz-titulo-dashboard-header);
  color: var(--clr-titulo-dashboard-header);
}
@media only screen and (max-width: 70em) {
  .header > .left > .titulo {
    --fz-titulo-dashboard-header: 1.08rem;
  }
}
.header > .left > .subtitulo {
  font-size: var(--fz-subtitulo-dashboard-header);
  color: var(--clr-subtitulo-dashboard-header);
  padding-top: 1rem;
}
@media only screen and (max-width: 70em) {
  .header > .left > .subtitulo {
    padding-top: 0.3rem;
    --fz-subtitulo-dashboard-header: 0.625rem;
  }
}
.header > .right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  min-width: 8.75rem;
}
@media only screen and (max-width: 40em) {
  .header > .right {
    width: 100%;
    align-items: flex-start;
    margin-top: 1rem;
    padding-top: 0.4rem;
    border-top: 1px solid var(--clr-lighttheme-50);
  }
}

.main {
  display: grid;
  width: 100%;
  margin-top: 3.125rem;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
  gap: 2rem;
}
.main > * {
  width: 100%;
}
@media only screen and (min-width: 40em) and (max-width: 70em) {
  .main {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media only screen and (max-width: 40em) {
  .main {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
}

.assinatura {
  font-size: 11px;
  text-align: center;
}
.assinatura > a {
  color: #3e52a3;
}

body > .background {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 30%;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: var(--clr-background-dashboard);
}

.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
}
@media only screen and (max-width: 70em) {
  .toggle {
    width: 100%;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 40em) {
  .toggle {
    height: 60%;
  }
}
.toggle label > .text-label {
  width: 100%;
  min-width: 4.4375rem;
  font-weight: 700;
  font-size: var(--fz-labeltext);
  color: var(--clr-labeltext-theme);
  opacity: var(--theme-label);
}
@media only screen and (max-width: 70em) {
  .toggle label > .text-label {
    --fz-labeltext: 0.625rem;
  }
}
@media only screen and (min-width: 40em) and (max-width: 70em) {
  .toggle label > .text-label {
    text-align: right;
  }
}
.toggle > .wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  margin-left: 1rem;
  width: 100%;
  max-width: 2.75rem;
}
@media only screen and (max-width: 70em) {
  .toggle > .wrapper {
    margin-left: 0;
  }
}
.toggle > .wrapper > input {
  width: 18px;
  height: 18px;
}
.toggle > .wrapper > input:nth-child(2) {
  margin-left: 2px;
}
.toggle > .wrapper > .background {
  position: absolute;
  transform: translate(-50%);
  display: block;
  left: 50%;
  width: 100%;
  height: 24px;
  background: var(--clr-background-toggle);
  border-radius: 20px;
  opacity: 0.5;
  pointer-events: none;
}
.toggle > .wrapper > .btn {
  z-index: 2;
  position: absolute;
  transform: translateY(-50%);
  display: block;
  top: 50%;
  left: 0%;
  width: 18px;
  height: 18px;
  margin-left: 3px;
  border-radius: 20px;
  background-color: var(--clr-button-toggle);
  transition: left 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  pointer-events: none;
  opacity: 0.8;
}
.toggle > .wrapper > .btn:hover {
  left: 46%;
}

.card {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 1rem 3rem;
  border-radius: 0.3125rem;
  background: var(--clr-background-card);
}
.card > :nth-child(even) {
  text-align: right;
}
.card:hover {
  cursor: pointer;
  background: var(--clr-activebackground-card);
}

.cardgrande {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1.75rem 0 1.25rem 0;
  border-radius: 0.3125rem;
  color: #63687e;
  background: var(--clr-background-cardgrande);
}
.cardgrande::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 4px;
  background: var(--clr-basetext);
}
.cardgrande:hover {
  cursor: pointer;
  background: var(--clr-activebackground-cardgrande);
}
.cardgrande > .redesocial {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cardgrande > .redesocial > .icone {
  font-size: 1.25rem;
  color: var(--clr-basetext);
  margin-right: 0.5rem;
}
.cardgrande > .redesocial > .tag {
  color: #63687e;
}
.cardgrande > .numero {
  margin-top: 1.125rem;
  font-size: var(--fz-650);
  text-align: center;
}
.cardgrande > .texto {
  margin-top: 0.25rem;
  color: var(--clr-basetext);
  font-size: var(--fz-50);
  font-weight: 400;
  text-align: center;
  letter-spacing: 4px;
  text-transform: uppercase;
}
.cardgrande > .crescimento {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.375rem;
}
.cardgrande > .crescimento.positivo > * {
  color: #1db489;
}
.cardgrande > .crescimento.negativo > * {
  color: #dc414c;
}
.cardgrande > .crescimento > .icone.flecha {
  width: 8px;
  height: 4px;
  margin-right: 0.1rem;
  transform: translateY(-25%);
}
.cardgrande > .crescimento > .icone.flecha.-cima {
  background: url(../images/icon-up.svg);
}
.cardgrande > .crescimento > .icone.flecha.-baixo {
  background: url(../images/icon-down.svg);
}
.cardgrande > .crescimento > .numero {
  padding-right: 0.1rem;
}
.cardgrande > .crescimento > :nth-child(even) {
  text-align: left;
}

.cardgrande.facebook::before {
  background: #198ff5;
}
.cardgrande.facebook > .redesocial > .icone.facebook {
  width: 20px;
  height: 20px;
  background: url(../images/icon-facebook.svg);
}

.cardgrande.twitter::before {
  background: #1ca0f2;
}
.cardgrande.twitter > .redesocial > .icone.twitter {
  width: 21px;
  height: 17px;
  background: url(../images/icon-twitter.svg);
}

.cardgrande.instagram::before {
  background: linear-gradient(90deg, var(--clr-primarylinear-100));
}
.cardgrande.instagram > .redesocial > .icone.instagram {
  width: 20px;
  height: 20px;
  background: url(../images/icon-instagram.svg);
}

.cardgrande.youtube::before {
  background: #c4032a;
}
.cardgrande.youtube > .redesocial > .icone.youtube {
  width: 20px;
  height: 20px;
  background: url(../images/icon-youtube.svg);
}

.dashboard > .resumo > .titulo {
  font-size: var(--fz-titulo-dashboard-resumo);
  color: var(--clr-lighttheme-500);
}
.dashboard > .resumo > .case-cartoes {
  display: grid;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: auto;
  align-self: center;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(8, 1fr);
}
.dashboard > .resumo > .case-cartoes > * {
  width: 100%;
}
@media only screen and (min-width: 40em) {
  .dashboard > .resumo > .case-cartoes {
    align-self: center;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(4, 1fr);
    width: auto;
  }
}
@media only screen and (min-width: 70em) {
  .dashboard > .resumo > .case-cartoes {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr;
    width: 100%;
  }
}

:root {
  --clr-background-toggle: var(--clr-lighttheme-50);
  --clr-button-toggle: var(--clr-lighttheme-200);
  --clr-background-cardgrande: var(--clr-lighttheme-300);
  --clr-background-card: var(--clr-lighttheme-300);
  --clr-activebackground-cardgrande: var(--clr-lighttheme-600);
