  --theme-label-text: 1;
  --fz-labeltext: var(--fz-75);
  --clr-labeltext-theme: var(--clr-lighttheme-400);
  --clr-basetext: var(--clr-lighttheme-500);
  --clr-background-dashboard: var(--clr-lighttheme-200);
  --fz-titulo-dashboard-header: var(--fz-400);
